import DateFnsUtils from "@date-io/date-fns";
import {
    Button,
    FormControl,
    FormControlLabel,
    FormLabel,
    Grid,
    IconButton,
    InputAdornment,
    InputLabel,
    OutlinedInput,
    Radio,
    RadioGroup,
    TextField
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import {
    KeyboardDatePicker,
    MuiPickersUtilsProvider
} from "@material-ui/pickers";
import React from "react";
import { useForm } from "react-hook-form";
import { RouteComponentProps } from "react-router-dom";
import { CustomButton } from "../../../../components";
import { ILogInProps } from "../../model/ILoginProps";
import { IRegister, MODAL_USER_LOGIN } from "../../model/ILoginState";

interface IProps extends RouteComponentProps, ILogInProps {}

const RegisterForm: React.FC<IProps> = props => {
    const [isShowPass, setShowPass] = React.useState(false);
    const [isShowConfirmPass, setShowConfirmPass] = React.useState(false);
    const [selectedDate, setSelectedDate] = React.useState<Date>(
        new Date()
    );
    const [gender, setGender] = React.useState("female");

    const { register, handleSubmit, errors, watch } = useForm<IRegister>();

    const password = React.useRef({});
    password.current = watch("password", "");

    const handleDateChange = (date: Date) => {
        setSelectedDate(date);
    };

    const handleChangeGender = (event : any) => {
        setGender(event.target.value);
    };

    const onSubmit = (data: IRegister) => {
        const dataRegister = {
            ...data,
            dateOfBirth : selectedDate.toISOString(),
            gender
        };

        props.actions.toggleModal({
            type: MODAL_USER_LOGIN.MODAL_CONFIRM_REGISTER,
            data: dataRegister
        });
    };

    const { isProcessing } = props.store.LoginPage;

    return (
        <React.Fragment>
            <form className="form" onSubmit={handleSubmit(onSubmit)}>
                <TextField
                    id="fullName"
                    label="H Tn"
                    variant="outlined"
                    fullWidth
                    name="fullName"
                    inputRef={register({
                        required: "H v tn khng c b trng",
                        maxLength: {
                            value: 30,
                            message: "H tn khng c qu 50 k t"
                        },
                        minLength: {
                            value: 2,
                            message: "H tn t nht 2 k t"
                        },
                        pattern: {
                            value: /^[A-Z]+(?:\s[A-Z]+)+$/i,
                            message: "H v tn khng hp l"
                        }
                    })}
                />
                {errors.fullName && <p>{errors.fullName.message}</p>}
                <TextField
                    id="email"
                    label="Email"
                    variant="outlined"
                    fullWidth
                    name="email"
                    className="form-control"
                    inputRef={register({
                        required: "Email khng c b trng",
                        maxLength: {
                            value: 30,
                            message: "Email khng c qu 30 k t"
                        },
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                            message: "Email khng hp l"
                        }
                    })}
                />
                {errors.email && <p>{errors.email.message}</p>}
                <FormControl
                    className="form-control text-field"
                    variant="outlined"
                >
                    <InputLabel htmlFor="password">Mt khu</InputLabel>
                    <OutlinedInput
                        id="password"
                        name="password"
                        type={isShowPass ? "text" : "password"}
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={() => setShowPass(!isShowPass)}
                                    edge="end"
                                >
                                    {isShowPass ? (
                                        <Visibility />
                                    ) : (
                                        <VisibilityOff />
                                    )}
                                </IconButton>
                            </InputAdornment>
                        }
                        labelWidth={70}
                        inputRef={register({
                            required: "Mt khu khng c b trng",
                            maxLength: {
                                value: 30,
                                message: "Mt khu khng c qu 30 k t"
                            },
                            minLength: {
                                value: 8,
                                message: "Mt khu t nht 8 k t"
                            },
                            pattern: {
                                value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])/i,
                                message:
                                    "Mt khu phi cha ch hoa, ch thng, k t c bit v s"
                            }
                        })}
                    />
                </FormControl>
                {errors.password && <p>{errors.password.message}</p>}
                <FormControl
                    className="form-control text-field"
                    variant="outlined"
                >
                    <InputLabel htmlFor="confirmPassword">
                        Nhp li mt khu
                    </InputLabel>
                    <OutlinedInput
                        id="confirmPassword"
                        name="confirmPassword"
                        type={isShowConfirmPass ? "text" : "password"}
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={() =>
                                        setShowConfirmPass(!isShowConfirmPass)
                                    }
                                    edge="end"
                                >
                                    {isShowConfirmPass ? (
                                        <Visibility />
                                    ) : (
                                        <VisibilityOff />
                                    )}
                                </IconButton>
                            </InputAdornment>
                        }
                        labelWidth={70}
                        inputRef={register({
                            required: "Xc nhn mt khu khng c b trng",
                            validate: (value : any) =>
                                value === password.current ||
                                "Mt khu nhp li khng chnh xc"
                        })}
                    />
                </FormControl>
                {errors.confirmPassword && (
                    <p>{errors.confirmPassword.message}</p>
                )}
                <Grid container className="form-control" spacing={2}>
                    <Grid item xs={6}>
                        <TextField
                            id="phone"
                            label="S in thoi"
                            variant="outlined"
                            fullWidth
                            name="phone"
                            inputRef={register({
                                required: "S in thoi khng c b trng",
                                minLength: {
                                    value: 10,
                                    message: "S in thoi t nht 10 k t"
                                },
                                pattern: {
                                    value: /[0-9]$/i,
                                    message: "S in thoi khng hp l"
                                }
                            })}
                        />
                        {errors.phone && <p>{errors.phone.message}</p>}
                    </Grid>
                    <Grid item xs={6}>
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <KeyboardDatePicker
                                name="dateOfBirth"
                                id="date-picker-dialog"
                                label="Ngy sinh"
                                format="dd/MM/yyyy"
                                value={selectedDate}
                                onChange={(selectedDate) => handleDateChange(selectedDate as Date)}
                                KeyboardButtonProps={{
                                    "aria-label": "change date"
                                }}
                                fullWidth
                            />
                        </MuiPickersUtilsProvider>
                    </Grid>
                </Grid>
                <TextField
                    id="address"
                    label="a ch"
                    name="address"
                    placeholder="a ch"
                    multiline
                    fullWidth
                    variant="outlined"
                    className="form-control"
                    inputRef={register({
                        required: "a ch khng c b trng",
                        maxLength: {
                            value: 300,
                            message: "a ch khng c qu 300 k t"
                        }
                    })}
                />
                {errors.address && <p>{errors.address.message}</p>}
                <FormControl component="fieldset" className="form-control">
                    <FormLabel component="legend">Gii tnh</FormLabel>
                    <RadioGroup
                        aria-label="gender"
                        name="gender"
                        value={gender}
                        onChange={handleChangeGender}
                        className="radio-group"
                    >
                        <FormControlLabel
                            value="female"
                            control={<Radio />}
                            label="N"
                        />
                        <FormControlLabel
                            value="male"
                            control={<Radio />}
                            label="Nam"
                        />
                        <FormControlLabel
                            value="other"
                            control={<Radio />}
                            label="Khc"
                        />
                    </RadioGroup>
                </FormControl>
                <CustomButton
                    type="submit"
                    fullWidth
                    variant="contained"
                    className="btn-login btn-register"
                    label="ng k"
                />
            </form>
        </React.Fragment>
    );
};

export default RegisterForm;
